import React, { Component, Fragment } from "react";
import styled from "styled-components";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Text from "./components/Typography";
import Box from "./components/Box";
import { FooterButton } from "./components/Button";
import { ArrowRight, ArrowLeft, Menu, Moon } from "react-feather";
import { animated, Transition } from "react-spring";
import { MemoryRouter as Router, Route, Switch, Link } from "react-router-dom";

const Wrapper = styled.div`
  width: 100vw;
  height: 100vh;
`;

const Header = styled.div`
  height: 44px;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  background-color: white;
  transition: box-shadow 400ms ease;
  align-items: center;
  display: flex;
  padding: 0px 16px;
  justify-content: center;
  border-bottom: 1px solid #ebebeb;
`;

const PageContainer = ({
  children,
  style,
  title,
  hideHeader = false,
  ...props
}) => (
  <Fragment>
    {!hideHeader && <Header>{title}</Header>}
    <animated.div
      style={{
        position: "absolute",
        top: 0,
        left: 0,
        width: "100vw",
        height: "100vh",
        backgroundColor: props.backgroundColor,
        display: "flex",
        flexDirection: "column",
        paddingTop: 44,
        ...style
      }}
    >
      {children}
    </animated.div>
  </Fragment>
);

const Intro = ({ transitionValues }) => (
  <PageContainer style={transitionValues} title="Pathogen" />
);

const InputField = styled.input`
  display: flex;
  border: 2px solid;
  border-color: #e9ecef;
  padding: 12px 12px;
  border-radius: 4px;
  background-color: #f7f8f9;
  width: 100%;
  font-size: 15px;
  outline: none;
  transition: all 150ms;

  &:focus {
    border-color: #87b1f4;
  }
`;

const Label = styled.label`
  padding: 4px;
`;

const Input = ({ label, hideBottomSpace, ...props }) => (
  <Box flexDirection="column">
    {label && (
      <Box paddingY={2}>
        <Text as="label" type="caption" style={{ color: "#797978" }}>
          {label}
        </Text>
      </Box>
    )}
    <InputField {...props} />
  </Box>
);

const Frame = ({ children }) => (
  <Box paddingX={1} paddingY={1} flexDirection="column">
    <Box height={22} width={375} style={{ background: "black" }} />
    <Box
      height={667}
      width={375}
      style={{
        border: "1px solid #444",
        borderTop: "none"
      }}
    >
      {children}
    </Box>
  </Box>
);

const Block = props => <Box {...props} display="block" />;

const TextArea = styled.textarea`
  border: 2px solid;
  border-color: #e9ecef;
  padding: 12px 12px;
  border-radius: 4px;
  background-color: #f7f8f9;
  width: 100%;
  font-size: 15px;
  outline: none;
  transition: all 150ms;
  resize: none;
  height: 80px;

  &:focus {
    border-color: #87b1f4;
  }
`;

const Spacer = ({ height }) => <Block height={height} />;

const CreateCampaign = ({ transitionValues }) => (
  <PageContainer
    style={transitionValues}
    backgroundColor="white"
    title="Create Campaign"
  >
    <Box flex={1} style={{ overflow: "scroll" }} flexDirection="column">
      <Block style={{ overflowY: "scroll" }}>
        <Block paddingX={2} flexDirection="column">
          <Input
            autoFocus
            placeholder="Paul the bard..."
            label="Campaign Name"
          />
          <Spacer height={8} />
          <Box paddingY={2}>
            <Text as="label" type="caption" style={{ color: "#797978" }}>
              Description
            </Text>
          </Box>
          <TextArea placeholder="There once was a man named pablo..." />
        </Block>
      </Block>
    </Box>
    <Box alignItems="center" justifyContent="center" paddingX={2} paddingY={2}>
      <FooterButton style={{ backgroundColor: "#181a2a" }}>
        Create Campaign
      </FooterButton>
    </Box>
  </PageContainer>
);

const ButtonTest = styled.div`
  padding: 5px 15px;
  background-color: #666;
  cursor: pointer;
  user-select: none;
  -webkit-user-select: none;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  transition: box-shadow 0.3s cubic-bezier(0.25, 0.8, 0.25, 1),
    background-color 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  &:hover {
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    background-color: #777;
  }
  &:active {
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    background-color: #aaa;
  }
`;

const createConfig = configs => name => {
  return configs[name] ? configs[name] : null;
};

const config = createConfig({
  opacity: {
    tension: 330,
    friction: 22
  },
  transform: {
    tension: 330,
    friction: 22
  }
});

class App extends Component {
  render() {
    return (
      <Router>
        <Route
          render={({ location }) => (
            <Transition
              native
              config={config}
              keys={location.pathname}
              from={{
                opacity: 0,
                transform: "translate3d(100%,0,0)"
              }}
              enter={{
                opacity: 1,
                transform: "translate3d(0%,0,0)"
              }}
              leave={{
                opacity: 0,
                transform: "translate3d(-50%,0, 0)"
              }}
            >
              {interpolated => (
                <Switch location={location}>
                  <Route
                    exact
                    path="/"
                    render={props => (
                      <Intro {...props} transitionValues={interpolated} />
                    )}
                  />
                  <Route
                    exact
                    path="/campaign/create"
                    render={props => (
                      <CreateCampaign
                        {...props}
                        transitionValues={interpolated}
                      />
                    )}
                  />
                </Switch>
              )}
            </Transition>
          )}
        />
      </Router>
    );
  }
}

const state = {
  campaigns: [],
  campaignsById: {},
  hasData: false
};

export default App;



